<template>
  <div>
    <section class="head">
      <div>
        <a @click="goBack" class="iconfont back"> &#xe614; </a>
        <a class="icon icon_manue"> manue </a>
      </div>
      <h1>Sport Top For Women</h1>
      <div class="chartWrap">
        <span class="share iconfont"> &#xe650; </span>
        <span class="bag"> <em class="font10"> 9+ </em> </span>
        <a class="iconfont cart"> &#xe669; </a></div>
    </section>

    <section class="exchangeContent">
      <div class="blocks">
        <div class="proWrap clearfix">
          <div class="imgWrap">
            <img :src="item_child.img" class="img">
          </div>
          <div class="numRight">
            <span class="iconfont"> &#xe619; </span>
            <span> {{item_child.num}} </span>
          </div>
          <div class="rightMain">
            <p class="name"> {{item_child.name}} </p>
            <p class="size"> {{item_child.size}} </p>
          </div>
        </div>
        <div class="commonRow">
          <p class="tittle"> Application Qty: </p>
          <div class="gw_num clearfix">
            <em class="jian iconfont"> &#xe613; </em>
            <input type="number" value="1" class="num"/>
            <em class="add iconfont"> &#xe715; </em>
          </div>
        </div>
      </div>

      <div class="blocks mt10">
        <div class="commonRow" @click="showReason">
          <p class="tittle"> Select Reason: <sup class="require">*</sup> </p>
          <div class="rightCol">
           <span class="color999">  {{resonVal.label}} </span> <span class="iconfont arrowDown"> &#xe671; </span>
          </div>
        </div>
      </div>

      <div class="blocks mt10">
        <div class="commonRow" >
          <p class="tittle"> Problem Description <sup class="require">*</sup> </p>
        </div>
        <div class="txt">
          <textarea  placeholder="Min 20 Characters">

          </textarea>
        </div>
      </div>

    </section>

    <div class="exchangeBtnwrap">
      <van-button size="large" type="danger"> Apply </van-button>
    </div>

    <van-popup v-model="reasonShow" position="bottom">
      <div class="resonList">
        <a class="close" @click="reasonShow=false"> Cancel </a>
        <ul class="clearfix">
          <li v-for="(item,index) in reasonList" :class="{'red':item.value==temp.value}"  @click="temp=item">
            <span class="iconfont radio"> {{item.value==temp.value?"&#xe63a;":"&#xe608;"}} </span>
            <div>
              <p class="name"> {{item.label}} </p>
            </div>
          </li>
        </ul>
        <div class="btnWrap mt20">
          <van-button type="danger"  @click="confirmReason" size="large">Confirm</van-button>
        </div>
      </div>
    </van-popup>


  </div>
</template>

<script>
export default {
  name: 'searchResult',
  data () {
    return {
      item_child:{
        "img":"static/images/beautyCarePro1.png",
        "name":"Name of the store and  shopeone Name of the store and  shopeone",
        "size":"White; L",
        "num":19,
      },
      resonVal:{
        "label":"",
        "value":0
      },
      temp:{},
      reasonList:[
        {
          "label":"Quality problem",
          "value":1
        },
        {
          "label":"Product is different with the description",
          "value":2
        },
        {
          "label":"Produt is damaged",
          "value":3
        },
        {
          "label":"Lack spare parts",
          "value":4
        },
        {
          "label":"Size,color,model,etc are inappropriate",
          "value":5
        },
        {
          "label":"I just don't want it",
          "value":6
        },
        {
          "label":"Product price has fallen",
          "value":7
        },
        {
          "label":"Wrong delivery or product",
          "value":8
        },
        {
          "label":"Other reason",
          "value":9
        }
      ],
      reasonShow:false

    }
  },
  computed: {
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
    },
    showReason(){
      this.temp={
        "label":this.resonVal.label,
        "value":this.resonVal.value
      }
      this.reasonShow=true;
    },
    confirmReason(){
      this.resonVal={
        "label":this.temp.label,
        "value":this.temp.value
      }
      this.reasonShow=false;
    },
    goBack(){
      this.$router.back(-1)
    }


  }
}
</script>

<style lang="scss" scoped>
  @import "../common/css/orderList.scss";
  .close{font-size: 15px;}
</style>



